import { useEffect, useState } from 'react';
import {
  Container,
  ContentContainer,
  HeaderContainer,
  AdminCategory,
  AdminCategories,
  AdminGoBack,
  AdminBarbershopContainer,
  IconContainer,
} from './AdminPainel.Style';
import { fetchNui } from '../../helpers/fetchNui';
import { useNavigate } from 'react-router-dom';
import PlusIcon from './icons/PlusIcon';
import TrashIcon from './icons/TrashIcon';
import TeleportIcon from './icons/TeleportIcon';
import { Strip } from '../../components/Strip/Strip';
import { DelBarber } from './components/DelBarber';
import { CreateBarber } from './components/CreateBarber';

export interface IBarbershop {
  id: number;
  name: string;
  coords: { x: number; y: number; z: number };
  blip: boolean;
}

export function AdmBarber() {
  const [barbershops, setBarbershops] = useState<IBarbershop[]>([
    {
      coords: { x: 0, y: 0, z: 0 },
      blip: false,
      name: 'Praa',
      id: 1,
    },
  ]);
  const navigate = useNavigate();
  const keydown = (event: any) => {
    switch (event.code) {
      case 'Escape':
        fetchNui('AdminPainel::Close');
        break;
    }
  };

  useEffect(() => {
    fetchNui('AdminPainel::GetBarbershops', {}).then((data) => {
      if (data) {
        setBarbershops(data);
      }
    });
  }, [setBarbershops, barbershops]);

  useEffect(() => {
    window.addEventListener('keydown', keydown);
    return () => {
      window.removeEventListener('keydown', keydown);
    };
  }, [keydown]);

  return (
    <Container
      style={{
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
      }}
    >
      <ContentContainer
        style={{
          position: 'relative',
        }}
      >
        <AdminGoBack
          onClick={() => {
            navigate('/Admin', { replace: true });
          }}
        >
          Voltar
        </AdminGoBack>
        <HeaderContainer>
          <Strip />
          <h2>
            <b>Barbearias</b>
          </h2>
          <Strip />
        </HeaderContainer>

        <CreateBarber />

        <AdminCategories
          style={{
            flexDirection: 'column',
            justifyContent: 'flex-start',
            alignItems: 'flex-start',
            overflowY: 'auto',
            marginTop: '2vh',
            width: '97%',
          }}
        >
          {barbershops.map((barbershop, index) => (
            <AdminBarbershopContainer>
              <h2>
                {barbershop.id} - {barbershop.name}
              </h2>
              <p>
                Coordenadas: {barbershop.coords.x.toFixed(2)}, {barbershop.coords.y.toFixed(2)}, {barbershop.coords.z.toFixed(2)} | Blip:{' '}
                {barbershop.blip ? 'Sim' : 'No'}
              </p>
              <div
                style={{
                  display: 'flex',
                  width: '25%',
                  height: '100%',
                  gap: '0.5vw',
                  alignSelf: 'center',
                  justifyContent: 'flex-end',
                }}
              >
                <IconContainer
                  onClick={() => {
                    fetchNui('AdminPainel::Close');
                    fetchNui('AdminPainel::Teleport', { coords: barbershop.coords });
                  }}
                >
                  <TeleportIcon />
                </IconContainer>
                <IconContainer bg="rgba(255, 0, 0, 0.4)">
                  <DelBarber
                    id={barbershop.id}
                    blip={barbershop.blip}
                    coords={barbershop.coords}
                    name={barbershop.name}
                    setBarbershops={setBarbershops}
                  />
                </IconContainer>
              </div>
            </AdminBarbershopContainer>
          ))}
        </AdminCategories>
      </ContentContainer>
    </Container>
  );
}
