import { useEffect } from 'react';
import { Container, ContentContainer, AdminCategory, AdminCategories } from './AdminPainel.Style';
import { fetchNui } from '../../helpers/fetchNui';
import { Strip } from '../../components/Strip/Strip';
import Cabelo from '../../assets/images/cabelo.png';
import Corpo from '../../assets/images/corpo.png';
import { useNavigate } from 'react-router-dom';

export function AdmPainel() {
  const navigate = useNavigate();
  const keydown = (event: any) => {
    switch (event.code) {
      case 'Escape':
        fetchNui('AdminPainel::Close');
        break;
    }
  };

  useEffect(() => {
    window.addEventListener('keydown', keydown);
    return () => {
      window.removeEventListener('keydown', keydown);
    };
  }, [keydown]);

  return (
    <Container
      style={{
        justifyContent: 'center',
      }}
    >
      <ContentContainer>
        <AdminCategories>
          <AdminCategory
            bg={Corpo}
            onClick={() => {
              navigate('/Admin/Chars');
            }}
          >
            <span>
              <Strip />
              PERSO
              <br />
              NAGENS
              <Strip />
            </span>
          </AdminCategory>
          <AdminCategory
            bg={Cabelo}
            onClick={() => {
              navigate('/Admin/Barbers');
            }}
          >
            <span>
              <Strip />
              BARBE
              <br />
              ARIAS
              <Strip />
            </span>
          </AdminCategory>
        </AdminCategories>
      </ContentContainer>
    </Container>
  );
}
