import { ColorButton, ColorsContainer } from './ColorPicker.Styled';

interface IColorPickerComponent {
  colors: any;
  functionOnClick: (colorIndex: number) => void;
}

export function ColorPickerComponent({ colors, functionOnClick }: IColorPickerComponent) {
  return (
    <ColorsContainer>
      {colors?.map((color: string, colorIndex: number) => (
        <ColorButton onClick={() => functionOnClick(colorIndex)} bg={color}></ColorButton>
      ))}
    </ColorsContainer>
  );
}
