import { useState } from 'react';
import { CSSTransition } from 'react-transition-group';
import { Button, EditCharacterContainer, InfoContainer, OnlineStatus } from '../AdminPainel.Style';
import { ICharacter } from '../AdmChars';
import { DelChar } from './DelChar';
import { Strip } from '../../../components/Strip/Strip';
import { fetchNui } from '../../../helpers/fetchNui';

interface ICharacterProps {
  character: ICharacter;
}

export function EditChar({ character }: ICharacterProps) {
  const [show, setShow] = useState(false);
  return (
    <>
      <CSSTransition in={show} timeout={800} classNames="fade" unmountOnExit>
        <EditCharacterContainer>
          <h2 onClick={() => setShow(false)}>X</h2>
          <InfoContainer style={{ gap: '1vw' }}>
            <img
              src={character.image || ''}
              alt=""
              onError={(e: any) => {
                e.target.src = 'https://uxwing.com/wp-content/themes/uxwing/download/peoples-avatars/no-profile-picture-icon.png';
                e.onerror = null;
              }}
            />
            <h1>{character.id}</h1>
            <Strip />
            <h1>{character.name}</h1>
            <OnlineStatus color={character.online ? 'online' : 'offline'} />
            <div></div>
          </InfoContainer>
          <Button
            disabled={!character.online}
            onClick={() => {
              fetchNui('AdminPainel::SendCreatorCharacter', { id: character.id });
            }}
          >
            Enviar para criao
          </Button>
          <DelChar character={character as ICharacter} />
        </EditCharacterContainer>
      </CSSTransition>
      <Button
        onClick={() => {
          setShow(!show);
        }}
      >
        Editar
      </Button>
    </>
  );
}
