import { ErrorIcon } from '../Input/Input.Style';
import { InputSmall } from './InputSmall.Style';

interface IInputSmall {
  option: any;
  menuActive?: any;
  functionOnChange: any;
  shouldValidate: boolean;
  functionToValidate?: () => void;
  max?: any;
  min?: any;
  value?: any;
}

export function InputSmallComponent({ option, menuActive, functionOnChange, shouldValidate, functionToValidate, max, min, value }: IInputSmall) {
  return (
    <div style={{ position: 'relative', width: '3vw' }}>
      <InputSmall
        type="number"
        value={value}
        placeholder={option.placeholder}
        min={min ? min : option.min}
        max={max ? max : option.max}
        style={menuActive && menuActive.options![0].options![0].error ? { border: '1px solid red' } : ''}
        onChange={(e: any) => {
          functionOnChange(e.target.value);

          if (shouldValidate && functionToValidate) {
            functionToValidate();
          }
        }}
      />

      {menuActive && menuActive.options![0].options![0].error && (
        <ErrorIcon
          className="fa fa-warning"
          style={{
            position: 'absolute',
            left: '3px',
            top: '35%',
            fontSize: '0.7rem',
          }}
        ></ErrorIcon>
      )}
    </div>
  );
}
