import { useState } from 'react';
import { useEffect } from 'preact/hooks';
import gsap from 'gsap';

import { BorderRight, Menu, MenuItem, MenuItemP, MenuWrapper, MenuWrapperLayer, ParagraphMenuSelectedContainer } from './WheelMenu.Style';

import { Strip } from '../Strip/Strip';
import { MenuItemSpan, ParagraphLeftMenuSelected } from '../../pages/Character/Creator.Style';

import { fetchNui } from '../../helpers/fetchNui';

interface IWheelMenu {
  menuActive: any;
  menu: any;
  menuWrapperRef: any;
  containerRef: any;
  menuWrapper: any;
  setActiveMenu: (index: number) => void;
  setMenu: (object: any) => void;
}

export function WheelMenuComponent({ menuActive, menu, menuWrapperRef, containerRef, menuWrapper, setActiveMenu }: IWheelMenu) {
  let mouseDraggable = false;
  let startingPos: number[] = [];
  let itemScrolled = 0;

  const [oldActiveMenu, setOldActiveMenu] = useState(0);
  const [activeMenuState, setActiveMenuState] = useState(0);

  useEffect(() => {
    if (menuWrapper && menuWrapper.current) {
      menuWrapper.current.removeEventListener('mousewheel', onScroll);

      menuWrapper.current.addEventListener('mousewheel', onScroll, {
        capture: true,
        passive: true,
      });

      return () => menuWrapper.current?.removeEventListener('mousewheel', onScroll);
    }
  }, []);

  const onScroll = (event: any) => {
    const items = document.querySelectorAll('.menu--item');

    if (event.deltaY > 0) {
      if (itemScrolled === items.length - 1) return;
      itemScrolled++;

      //@ts-ignore
      items[itemScrolled].click();
    } else {
      if (itemScrolled === 0) return;
      itemScrolled--;

      setActiveMenu(itemScrolled);
      //@ts-ignore
      items[itemScrolled].click();
    }
    const name = items[itemScrolled].querySelector('.menu--item--span')?.textContent;
    fetchNui('CharacterCreator::SetCameraIndex', { cam: name });
  };

  useEffect(() => {
    if (containerRef.current) {
      const mouseDown = (e: MouseEvent) => {
        mouseDraggable = false;
        startingPos = [e.pageX, e.pageY];
      };

      const mouseup = (e: MouseEvent) => {
        mouseDraggable = false;
        startingPos = [];
      };

      const mousemove = async (e: MouseEvent) => {
        if (!(e.pageX === startingPos[0] && e.pageY === startingPos[1])) mouseDraggable = true;
        if (mouseDraggable && startingPos[0] && startingPos[1])
          await fetchNui(`CharacterCreator::CameraHeight`, {
            x: e.pageX,
            y: e.pageY,
          });
      };

      containerRef.current.removeEventListener('mousedown', mouseDown);
      containerRef.current.removeEventListener('mouseup', mouseup);
      containerRef.current.removeEventListener('mousemove', mousemove);

      containerRef.current.addEventListener('mousedown', mouseDown);
      containerRef.current.addEventListener('mouseup', mouseup);
      containerRef.current.addEventListener('mousemove', mousemove);

      return () => [
        containerRef.current && containerRef.current.removeEventListener('mousedown', mouseDown),
        containerRef.current && containerRef.current.removeEventListener('mouseup', mouseup),
        containerRef.current && containerRef.current.removeEventListener('mousemove', mousemove),
      ];
    }
  }, [containerRef]);

  useEffect(() => {
    const diameter = document.querySelector('.menu')!.clientWidth / 2;
    const radius = document.querySelector('.menu')!.clientWidth / 2;
    const items = document.querySelectorAll('.menu--item');
    const interval = 0.244346;
    let angle = 0;
    const snap = 13.937;

    function positionItems(items: any) {
      items.forEach((item: any) => {
        let x = diameter + radius * Math.cos(angle) - item.clientWidth / 2;
        let y = diameter + radius * Math.sin(angle) - item.clientWidth / 2;

        item.style.left = `${x}px`;
        item.style.top = `${y}px`;

        angle += interval;
      });
    }

    positionItems(items);

    const onRotate = function (this: any) {
      items.forEach((item) => {
        gsap.set(item.firstElementChild, {
          rotation: -Number(gsap.getProperty('#menu--wrapper', 'rotation')),
          transformOrigin: 'center center',
        });
      });
    };

    function onItemClick(event: any) {
      const clickedItem = event.currentTarget;
      const index = Array.from(items).indexOf(clickedItem);

      items.forEach((item) => {
        item.classList.remove('menu--item--active');
      });

      clickedItem.classList.add('menu--item--active');
      const name = clickedItem.querySelector('.menu--item--span')?.textContent;
      fetchNui('CharacterCreator::SetCameraIndex', { cam: name });

      setActiveMenu(index);

      itemScrolled = index;

      const targetRotation = index * -snap;

      gsap.to('#menu--wrapper', 0.8, {
        rotation: targetRotation,
        onUpdate: onRotate,
      });
    }

    items.forEach((item) => {
      item.addEventListener('click', onItemClick);
    });
  }, []);

  useEffect(() => {
    if (activeMenuState === menuActive) return;

    if (activeMenuState !== menuActive) {
      setActiveMenuState(oldActiveMenu);
      setTimeout(() => {
        setOldActiveMenu(menuActive);
        setActiveMenuState(menuActive);
      }, 100);
      return;
    }
  }, [menuActive]);

  return (
    <>
      <BorderRight className=""></BorderRight>

      <ParagraphMenuSelectedContainer>
        <Strip />

        <ParagraphLeftMenuSelected>{menuActive.name}</ParagraphLeftMenuSelected>
      </ParagraphMenuSelectedContainer>

      <>
        <MenuWrapperLayer className="menu--wrapper--layer" ref={menuWrapperRef}>
          <MenuWrapper id="menu--wrapper" className="menu--wrapper">
            <Menu className="menu">
              {menu.map((item: any, index: number) => (
                <MenuItem className={`menu--item ` + (index === 0 ? 'menu--item--active' : '')}>
                  <MenuItemSpan className="menu--item--span">
                    <MenuItemP>
                      <img src={item.icon} width={32} style={{ filter: 'invert(1)' }} />
                      {item.name}
                    </MenuItemP>
                  </MenuItemSpan>
                </MenuItem>
              ))}
            </Menu>
          </MenuWrapper>
        </MenuWrapperLayer>
      </>
    </>
  );
}
