import { Row } from '../../../pages/Character/Creator.Style';
import { InputContainerTitle, InputTypeRange, InputTypeRangeAndTexts } from './InputRange.Styled';

interface IInputRange {
  option: any;
  functionOnChange: (e: string) => void;
  max?: number;
  min?: number;
  justInput?: boolean;
}

export function InputRangeComponent({ option, functionOnChange, max, min, justInput }: IInputRange) {
  return justInput ? (
    <InputTypeRange
      type="range"
      value={option.value}
      step={option.step}
      min={min ? min : option.min}
      max={max ? max : option.max}
      onChange={(e: any) => {
        functionOnChange(e.target.value);
      }}
    ></InputTypeRange>
  ) : (
    <>
      <InputContainerTitle>{option.title}</InputContainerTitle>
      <InputTypeRangeAndTexts>
        <Row>
          <p>{option.options && option.options[0].title !== 'Me' ? option.min : option.options && option.options[0].title}</p>
          <p>{option.value}</p>
          <p>{option.options && option.options[1].title !== 'Pai' ? option.max : option.options && option.options[1].title}</p>
        </Row>
        <InputTypeRange
          type="range"
          value={option.value}
          step={option.step}
          min={min ? min : option.min}
          max={max ? max : option.max}
          onChange={(e: any) => {
            functionOnChange(e.target.value);
          }}
        ></InputTypeRange>
      </InputTypeRangeAndTexts>
    </>
  );
}
