import { useEffect } from 'react';
import { useState } from 'preact/hooks';
import { JSXInternal } from 'preact/src/jsx';
import { Routes, Route, useNavigate } from 'react-router-dom';
import { FadeIn } from './styles/globalStyle';
import { fetchNui } from './helpers/fetchNui';

import { CharacterSelector } from './pages/Character/Selector';
import { CharacterCreator } from './pages/Character/Creator';
import { SpawnSelector } from './pages/SpawnSelector/SpawnSelector';
import { AdmChars } from './pages/Admin/AdmChars';
import { AdmPainel } from './pages/Admin/AdmPainel';
import { AdmBarber } from './pages/Admin/AdmBarber';

type RoutesTypes = {
  path: string;
  component: JSXInternal.Element;
};

export function Router() {
  const [pageLoaded, setPageLoaded] = useState<string | undefined>(undefined);

  const navigate = useNavigate();

  const getBase64Image = (src: string, callback: Function) => {
    const img = new Image();
    img.crossOrigin = 'Anonymous';
    img.onload = () => {
      const canvas = document.createElement('canvas');
      const ctx = canvas.getContext('2d');
      let dataURL;
      canvas.height = img.naturalHeight;
      canvas.width = img.naturalWidth;
      ctx?.drawImage(img, 0, 0);
      dataURL = canvas.toDataURL();
      callback(dataURL);
    };

    img.src = src;
    if (img.complete || img.complete === undefined) {
      img.src = src;
    }
  };

  function Convert(pMugShotTxd: string, id: number) {
    let tempUrl = 'https://nui-img/' + pMugShotTxd + '/' + pMugShotTxd + '?t=' + String(Math.round(new Date().getTime() / 1000));
    if (pMugShotTxd == 'none') {
      tempUrl = 'https://upload.wikimedia.org/wikipedia/commons/thumb/b/bc/Unknown_person.jpg/434px-Unknown_person.jpg';
    }
    getBase64Image(tempUrl, function (dataUrl: string) {
      fetchNui('Answer', {
        Answer: dataUrl,
        Id: id,
      });
    });
  }

  const messageHandler = async (event: any) => {
    switch (event.data.action) {
      case 'Change:Route':
        setPageLoaded(event.data.name);

        navigate(event.data.message.route, { state: { name: event.data.name, arguments: event.data.message.arguments } });

        await fetchNui(`${event.data.name}::RouteChanged`, { name: event.data.name });

        break;

      case 'Close:Route':
        navigate('/');

        setPageLoaded(undefined);

        break;
      case 'convert':
        Convert(event.data.pMugShotTxd, event.data.id);
        break;
    }
  };

  useEffect(() => {
    window.addEventListener('message', messageHandler);

    return () => {
      window.removeEventListener('message', messageHandler);
    };
  }, [messageHandler]);

  useEffect(() => {
    fetchNui('NuiReady');
  }, []);

  const routes: RoutesTypes[] = [
    {
      path: '/',
      component: <></>,
    },
    {
      path: '/Character/Selector',
      component: <CharacterSelector />,
    },
    {
      path: '/Character/Creator',
      component: <CharacterCreator />,
    },
    {
      path: '/Spawn/Selector',
      component: <SpawnSelector />,
    },
    {
      path: '/Admin',
      component: <AdmPainel />,
    },
    {
      path: '/Admin/Chars',
      component: <AdmChars />,
    },
    {
      path: '/Admin/Barbers',
      component: <AdmBarber />,
    },
  ];

  return (
    <Routes>
      {routes.map((route: RoutesTypes) => (
        <Route path={route.path} element={<FadeIn>{route.component}</FadeIn>} />
      ))}
    </Routes>
  );
}
