import { ErrorIcon, InputLarge } from './Input.Style';

interface IInput {
  item: any;
  menuActive: any;
  functionOnChange: any;
  shouldValidate: boolean;
  functionToValidate?: () => void;
}

export function InputComponent({ item, menuActive, functionOnChange, shouldValidate, functionToValidate }: IInput) {
  return (
    <div style={{ position: 'relative', width: '65%' }}>
      <InputLarge
        placeholder={item.placeholder}
        style={menuActive.options![0].error ? { border: '1px solid red' } : ''}
        onChange={(e: any) => {
          functionOnChange(e.target.value);

          if (shouldValidate && functionToValidate) {
            functionToValidate();
          }
        }}
      />

      {menuActive.options![0].error && (
        <ErrorIcon
          className="fa fa-warning"
          style={{
            position: 'absolute',
            left: '25px',
            top: '35%',
            fontSize: '0.7rem',
          }}
        ></ErrorIcon>
      )}
    </div>
  );
}
