import { useEffect, useState } from 'react';
import {
  Bottom,
  Button,
  CharacterList,
  CharacterTexts,
  Container,
  DeleteCharacterButton,
  DeleteCharacterButtonsContainer,
  DeleteCharacterContainer,
  ServerLogo,
  Notify,
  ServerText,
  ServerTextBold,
} from './Selector.Style';
import Bg from '../../assets/img/bg.png';
import { FadeIn } from '../../styles/globalStyle';
import { useLocation } from 'react-router-dom';
import { fetchNui } from '../../helpers/fetchNui';
import { CSSTransition } from 'react-transition-group';
import Character from '../../components/character/Character';

export function CharacterSelector() {
  const location = useLocation();
  const [pageName] = useState('CharacterSelector');
  const [characters, setCharacters] = useState<any>(
    location.state?.arguments?.characters || [
      {
        id: 0,
        name: 'Novo Personagem',
        image: 'newCharacter.png',
      },
    ],
  );
  const [selectedCharacter, setSelectedCharacter] = useState(location.state?.arguments?.characters[0]?.id || 0);
  const [deleteCharacterContainerIsVisible, setDeleteCharacterContainerIsVisible] = useState(false);
  const [notify, setNotify] = useState<string>('');

  const selectCharacter = async (id: number) => {
    setSelectedCharacter(id);
    await fetchNui(`${pageName}::SelectCharacter`, { id });
  };

  const deleteCharacter = async (id: number) => {
    setDeleteCharacterContainerIsVisible(!deleteCharacterContainerIsVisible);
    const result = await fetchNui(`${pageName}::DeleteCharacter`, { id });
    if (result) {
      setCharacters(characters.filter((character: any) => character.id !== id));
      setSelectedCharacter(0);
    } else {
      setNotify('Erro ao excluir personagem!');
      setTimeout(() => {
        setNotify('');
      }, 5000);
    }
  };

  const createCharacter = async () => {
    const result = await fetchNui(`${pageName}::CreateCharacter`);
    if (!result) {
      setNotify('Limite de personagens atingido!');
      setTimeout(() => {
        setNotify('');
      }, 5000);
    }
  };

  const joinServer = async () => {
    await fetchNui(`${pageName}::JoinServer`, { id: selectedCharacter });
  };

  return (
    <FadeIn>
      <Container background={Bg}>
        <CSSTransition timeout={800} classNames="fade" unmountOnExit>
          <Notify>{notify}</Notify>
        </CSSTransition>
        <CSSTransition in={deleteCharacterContainerIsVisible} timeout={800} classNames="fade" unmountOnExit>
          <DeleteCharacterContainer>
            <h3>Tem certeza que deseja excluir o personagem?</h3>
            <p>
              <b>Ateno!</b> Voc no poder recuperar seu personagem novamente.
            </p>
            <DeleteCharacterButtonsContainer>
              <DeleteCharacterButton onClick={() => deleteCharacter(selectedCharacter)}>SIM</DeleteCharacterButton>
              <DeleteCharacterButton onClick={() => setDeleteCharacterContainerIsVisible(!deleteCharacterContainerIsVisible)}>
                NO
              </DeleteCharacterButton>
            </DeleteCharacterButtonsContainer>
          </DeleteCharacterContainer>
        </CSSTransition>
        <CharacterTexts>
          <ServerText>
            SELEO DE{' '}
            <ServerTextBold>
              <br></br>PERSONAGEM
            </ServerTextBold>
          </ServerText>
          <ServerLogo></ServerLogo>
        </CharacterTexts>
        <CharacterList>
          {characters.map((character: any) => (
            <>
              <Character character={character} />
              <Button onClick={() => selectCharacter(character.id)}>SELECIONAR PERSONAGEM</Button>
              <Button
                onClick={() => {
                  selectCharacter(character.id);
                  setDeleteCharacterContainerIsVisible(!deleteCharacterContainerIsVisible);
                }}
              >
                EXCLUIR
              </Button>
            </>
          ))}
        </CharacterList>
        <Bottom>
          {selectedCharacter > 0 && <Button onClick={joinServer}>ENTRAR</Button>}
          <Button onClick={createCharacter}>CRIAR PERSONAGEM</Button>
        </Bottom>
      </Container>
    </FadeIn>
  );
}
