import {
  Background,
  CenterContainer,
  Container,
  HeaderContainer,
  LocationInformations,
  LocationInformationsContainer,
  LocationInformationsLastCoords,
  SelectSpawnButton,
  ServerLogo,
} from './SpawnSelector.Style';
import LocationImage from '../../assets/img/imageLocationExample.png';

import 'leaflet/dist/leaflet.css';
import L from 'leaflet';
import 'leaflet-rotate';
import { useEffect, useState } from 'react';
import { useLocation } from 'react-router-dom';
import { fetchNui } from '../../helpers/fetchNui';
import Bg from '../../assets/img/bg_spawnselector.png';

interface ILocation {
  x?: number;
  y?: number;
  z?: number;
  label?: number;
  image?: string;
}

export function SpawnSelector() {
  const location = useLocation();
  const [spawnPoint, setSpawnPoint] = useState<ILocation>({});
  const [defaultPosition, setDefaultPosition] = useState(
    location.state?.arguments?.spawns || [
      { x: -1665.43, y: -537.9, label: 'Hotel Banner (Del Perro)' },
      {
        x: 767.14,
        y: -937.98,
        label: 'Ponto de Onibus (Popular Street)',
      },
      { x: 444.33, y: -667.69, label: 'Central de Onibus' },
      {
        x: 913.67,
        y: -188.64,
        label: 'Ponto de Taxi (Leste Vinewood)',
      },
      { x: 1126.87, y: 2668.39, label: 'Motel (Grand Senora)' },
      {
        x: 1983.15,
        y: 3756.63,
        label: 'Posto de Gasolina (Sandy Shores)',
        image: 'easternmotel.jpg',
      },
      {
        x: 1688.06,
        y: 4769.55,
        label: 'Fliperama (Grapeseed)',
      },
      {
        x: -352.0,
        y: 6164.37,
        label: 'Igreja (Paleto)',
      },
      {
        x: 138.79,
        y: 6634.17,
        label: 'Posto de Gasolina (Paleto)',
      },
    ],
  );

  useEffect(() => {
    const center_x = 117.3;
    const center_y = 172.8;
    const scale_x = 0.02072;
    const scale_y = 0.0205;

    const CUSTOM_CRS = L.extend({}, L.CRS.Simple, {
      projection: L.Projection.LonLat,
      scale: function (zoom: number) {
        return Math.pow(2, zoom);
      },
      zoom: function (sc: number) {
        return Math.log(sc) / 0.6931471805599453;
      },
      distance: function (pos1: { lng: number; lat: number }, pos2: { lng: number; lat: number }) {
        const x_difference: number = pos2.lng - pos1.lng;
        const y_difference: number = pos2.lat - pos1.lat;
        return Math.sqrt(x_difference * x_difference + y_difference * y_difference);
      },
      transformation: new L.Transformation(scale_x, center_x, -scale_y, center_y),
    });

    const SateliteStyle = L.tileLayer('mapStyles/styleSatelite/{z}/{x}/{y}.jpg', {
      minZoom: 0,
      maxZoom: 8,
      noWrap: true,
    });

    const customIcon = (icon: any) =>
      L.divIcon({
        className: 'custom-div-icon',
        html: `
        <div class="marker-pin">
          <div class="marker-pin-center">
            >
          </div>
        </div>
        `,
        iconSize: [36, 36],
        iconAnchor: [5, -19],
      });

    const map = L.map('map', {
      crs: CUSTOM_CRS,
      minZoom: 0,
      maxZoom: 5,
      preferCanvas: true,
      layers: [SateliteStyle],
      center: [1920, 400],
      zoom: 2,
      zoomSnap: 2.4,
      dragging: false,
      zoomControl: false,
      scrollWheelZoom: false,
      doubleClickZoom: false,
      attributionControl: false,
      bearing: -90,
      rotate: true,
      rotateControl: false,
    });

    defaultPosition &&
      defaultPosition.map((item: { x: number; y: number; label: string }) => {
        const marker = L.marker(map.getCenter(), {
          icon: customIcon('pin'),
        })
          .addTo(map)
          .bindPopup(`<div><h5>${item.label}</h5></div>`)
          .on('click', () => setSpawnPoint(item as any));
        const lat = item.y;
        const lng = item.x;
        const newLatLng = new L.LatLng(lat, lng);
        marker.setLatLng(newLatLng);
      });

    return () => {
      map.remove();
    };
  }, []);

  const selectSpawn = async () => {
    await fetchNui('SpawnSelector::SelectSpawn', spawnPoint);
  };

  return (
    <>
      <Background bg={Bg}></Background>
      <Container>
        <CenterContainer>
          <div
            id="map"
            style={{
              height: '80%',
              width: '81.2%',
              background: 'rgb(17 17 17)',
              overflow: 'hidden',
              filter: 'grayscale(1)',
              marginTop: '-4.4vw',
            }}
          ></div>
          <HeaderContainer>
            <h2>
              SELEO DE <br />
              <b>SPAWN</b>
            </h2>
            <ServerLogo />
          </HeaderContainer>
        </CenterContainer>

        {spawnPoint.label && (
          <LocationInformationsContainer>
            <img src={(spawnPoint.image && `nui://will_creator_v2/web-side/dist/spawns/${spawnPoint.image}`) || LocationImage} alt="" />

            <LocationInformations>
              <h4>{spawnPoint.label}</h4>
              <p>Selecione o local para spawnar</p>
            </LocationInformations>
            <SelectSpawnButton onClick={selectSpawn}>SELECIONAR SPAWN</SelectSpawnButton>
          </LocationInformationsContainer>
        )}
        <LocationInformationsLastCoords>
          <h4>ULTIMA LOCALIZAO</h4>
          <SelectSpawnButton onClick={() => fetchNui('SpawnSelector::SelectLastSpawn')}>SPAWNAR</SelectSpawnButton>
        </LocationInformationsLastCoords>
      </Container>
    </>
  );
}
