import { CharacterItem, CharacterName, CharacterStrip, Divisor, Subtitle, StripItem, Text, Value } from '../../pages/Character/Selector.Style';

interface Character {
  id: number;
  name: string;
  bank: number;
  phone: string;
  job: string;
}

const Character = ({ character }: { character: Character }) => {
  return (
    <CharacterItem>
      <CharacterName>
        {character.id}
        <CharacterStrip>
          <StripItem color="#fff"></StripItem>
          <StripItem color="#fff"></StripItem>
          <StripItem color="#fff"></StripItem>
        </CharacterStrip>
        {character.name}
      </CharacterName>
      <Divisor></Divisor>
      <Subtitle>
        <Text>
          BANCO: <Value>R${character.bank}</Value>
        </Text>
        <Text>
          TELEFONE: <Value>{character.phone}</Value>
        </Text>
        <Text>
          TRABALHO: <Value>{character.job}</Value>
        </Text>
      </Subtitle>
    </CharacterItem>
  );
};

export default Character;
