import styled from 'styled-components';

export const Container = styled.div`
  height: 100vh;
  width: 100vw;
  /* background: black;
  opacity: 0.8; */
  background-size: cover;
  display: flex;
  flex-direction: row;
  align-items: center;
  position: relative;

  overflow: hidden;
`;

export const ContentContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 8px 0px;

  width: 50vw;
  height: 50vh;
  background-color: rgba(190, 190, 190, 0.4);
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-radius: 3px;

  p {
    color: #fff;
    font-size: 0.75vw;
    align-self: flex-end;
    margin-right: 1.3vw;
    margin-top: 0.5vw;
  }
`;

export const HeaderContainer = styled.header`
  display: flex;
  align-items: center;
  justify-content: center;

  h2 {
    color: #fff;

    b {
      font-weight: 700;
    }
  }
`;

export const CharactersContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 95%;
  height: 100%;
  overflow-y: auto;
  margin-top: 1vh;
`;

export const CharacterContainer = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.4);
  border-radius: 3px;
  margin-bottom: 8px;
`;

export const CharacterInfo = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 3px 8px;

  img {
    width: 2.5vw;
    height: 2.5vw;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 0.5vw;
  }

  span {
    font-size: 0.7vw;
    font-weight: bold;
    color: #000;
  }
`;

export const InfosContainer = styled.div`
  display: flex;
  flex-direction: column;
`;

export const InfoContainer = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
`;

export const CharacterAge = styled.span`
  font-size: 0.7vw;
  color: #000;
  font-weight: bold;
`;

interface IButton {
  disabled?: boolean;
}

export const Button = styled.button<IButton>`
  width: 70%;
  height: 1.7vw;
  background-color: rgba(255, 255, 255, 0.6);

  font-size: 0.7vw;
  font-weight: 600;
  align-items: center;
  display: flex;
  justify-content: center;
  margin-right: 10px;
  transition: 0.5s;
  color: #000;
  border: none;

  &:hover {
    cursor: pointer;
    background-color: rgba(255, 255, 255, 1);
    color: black;
    transition: 0.5s;
    width: 85%;
    box-shadow: 0px 0px 3vw 0.1vw rgba(255, 255, 255, 0.3);
  }
  &:disabled {
    cursor: not-allowed;
    background-color: rgba(119, 118, 118, 0.6);
    color: #000;
    transition: 0.5s;
    width: 70%;
  }
`;

export const EditCharacterContainer = styled.div`
  height: 20vh;
  width: 35vw;
  background-color: #444444ff;
  padding: 20px;

  position: absolute;
  top: 50%;
  right: 50%;
  transform: translate(50%, -50%);
  text-align: center;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  border-radius: 3px;

  h1 {
    font-weight: bold;
    color: #fff;
  }

  img {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    object-fit: cover;
  }

  h2 {
    position: absolute;
    top: 0;
    right: 0;
    margin: 10px;
    font-size: 1vw;
    cursor: pointer;
    transition: 0.5s;
    font-weight: bold;
  }
`;

export const DeleteCharacterContainer = styled.div`
  background-color: #666666ff;
  padding: 20px;

  position: absolute;
  top: 50%;
  right: 50%;
  transform: translate(50%, -50%);
  text-align: center;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  border-radius: 3px;

  p {
    font-size: 1vw;

    b {
      font-weight: bold;
    }
  }

  div {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 1vw;
    margin-top: 1vw;
  }
`;

interface OnlineStatus {
  color: string;
}

export const OnlineStatus = styled.div<OnlineStatus>`
  width: 0.5vw;
  height: 0.5vw;
  border-radius: 50%;
  margin-left: 0.5vw;
  background-color: ${(props) => (props.color === 'online' ? '#00ff00' : '#ff0000')};
`;

export const AdminCategories = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
`;

interface IAdminCategory {
  bg?: string;
}

export const AdminCategory = styled.div<IAdminCategory>`
  width: 95%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  margin: 0.2vw;
  padding: 3px 8px;
  display: flex;
  align-items: flex-end;
  position: relative;
  overflow: hidden;
  transition: all 0.3s ease;

  background-image: url(${(props) => props.bg});
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-blend-mode: ${(props) => props.bg && 'overlay'};

  span {
    font-size: 3vw;
    font-weight: 900;
    color: #000;
    text-shadow: 0 0 0.5vw rgba(255, 255, 255, 0.6), 0 0 1vw rgba(0, 0, 0, 0.4);
    text-transform: uppercase;
    user-select: none;
    letter-spacing: 0.1vw;
    line-height: 1.2;
  }

  &:hover {
    cursor: pointer;
    transform: scale(1.05);
    background-color: rgba(255, 255, 255, 0.3);
    box-shadow: 0 0 2vw rgba(255, 255, 255, 0.2);
  }
`;

export const AdminGoBack = styled.div`
  position: absolute;
  top: 1vh;
  left: 1vw;
  cursor: pointer;
  transition: 0.5s;
  font-size: 1vw;
  font-weight: bold;
  color: #fff;
  background-color: rgba(255, 255, 255, 0.2);
  padding: 0.3vw;
  border-radius: 0.2vw;

  &:hover {
    transition: 0.5s;
    background-color: rgba(255, 255, 255, 0.4);
  }
`;

export const AdminBarbershopContainer = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 5vh;
  background-color: rgba(255, 255, 255, 0.4);
  border-radius: 0.5vh;
  margin-bottom: 0.5vh;
  transition: 0.5s;
  box-shadow: 0px 0px 3vw 0.1vw rgba(0, 0, 0, 0.1);

  h2 {
    margin-left: 0.5vw;
    color: #fff;
    font-weight: bold;
  }

  p {
    align-self: center;
  }
`;

export const IconContainer = styled.div<IAdminCategory>`
  width: 25%;
  height: 100%;
  border-radius: 0.5vh;
  background-color: ${(props) => (props.bg ? props.bg : 'rgba(255, 255, 255, 0.4)')};
  display: flex;
  align-items: center;
  justify-content: center;
  transition: 0.5s;
  cursor: pointer;

  &:hover {
    transition: 0.5s;
    box-shadow: 0px 0px 3vw 0.1vw rgba(0, 0, 0, 0.3);
  }

  svg {
    width: 1.2vw;
    height: 1.2vw;
    color: #fff;
  }
`;

export const SliderToggleStyle = styled.label`
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 34px;
  transition: 0.4s;

  &::before {
    position: absolute;
    content: '';
    height: 22px;
    width: 22px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    border-radius: 50%;
    transition: 0.4s;
  }

  input:checked + & {
    background-color: #2196f3;
  }

  input:checked + &::before {
    transform: translateX(20px);
  }

  &:hover {
    background-color: rgba(255, 255, 255, 0.3);
  }
`;
