import { useEffect, useState } from 'preact/hooks';
import { useRef } from 'react';
import {
  Container,
  PageHeader,
  ServerLogo,
  SideContainerLeft,
  SideContainerRight,
  MenuTitle,
  NormalText,
  HeaderText,
  HeaderTextBold,
  MenuOptionsContainer,
  Text600,
  ParentImageContainer,
  ParentContainer,
  ParentNameContainer,
  Row,
  Button,
  ContentContainer,
  ButtonsContainer,
  InputContainerBox,
  CenterContainer,
  ErrorIcon,
} from './Creator.Style';
import { Strip } from '../../components/Strip/Strip';
import { InputComponent } from '../../components/Fields/Input/Input';
import { InputSmallComponent } from '../../components/Fields/InputSmall/InputSmall';
import { InputRadioComponent } from '../../components/Fields/InputRadio/InputRadio';
import { InputRangeComponent } from '../../components/Fields/InputRange/InputRange';
import { InputContainerTitle, InputTypeRangeAndTexts } from '../../components/Fields/InputRange/InputRange.Styled';
import { WheelMenuComponent } from '../../components/WheelMenu/WheelMenu';
import { ColorPickerComponent } from '../../components/Fields/ColorPicker/ColorPicker';
import { ImagePickerComponent } from '../../components/Fields/ImagePicker/ImagePicker';
import { CHARACTER_CREATOR_MENU, faceNames, ICharCreatorMenu } from './Creator.Menu';
import { fetchNui } from '../../helpers/fetchNui';
import RodaImage from '../../assets/images/Roda.png';

export function CharacterCreator() {
  const [activeMenu, setActiveMenu] = useState<number>(0);
  const [genderSelected, setGenderSelected] = useState('mp_m_freemode_01');
  const [parentImage, setParentImage] = useState<string[]>([]);
  const [menu, setMenu] = useState<ICharCreatorMenu[]>(CHARACTER_CREATOR_MENU);
  const [barbershop, setBarbershop] = useState<Record<string, string[]>>({
    ['Olhos']: ['eyeColor', 'eyebrowsStyle', 'eyebrowsOpacity', 'eyebrowsColor', 'eyebrowsColor2', 'eyebrowsHeight', 'eyebrowsWidth'],
    ['Nariz']: [],
    ['Bochecha']: [],
    ['Rosto']: ['lips'],
    ['Cabelo']: ['hair', 'hairColor', 'hairColor2'],
    ['Barba']: ['beard', 'beardColor'],
    ['Maquiagem']: ['lipstick', 'lipstickColor', 'makeup'],
    ['Aspectos']: [],
    ['Corpo']: ['bodyHair', 'bodyHairColor'],
    ['Outfit']: [],
    ['Genetica']: [],
  });

  const menuWrapper = useRef<HTMLDivElement>(null);
  const containerRef = useRef<HTMLDivElement>(null);
  const newParentImage = [...parentImage];

  const messageHandler = async (event: any) => {
    switch (event.data.action) {
      case 'UPDATE_TEXTURE':
        const textureMaximum = event.data.data;
        const newMenu: any = [...menu];

        newMenu.map((item: any, index: number) => {
          if (item.name == 'Outfit') {
            item.options.map((x: any, optIndex: number) => {
              x.options.map((y: any, optIndex2: number) => {
                if (y.drawableId) {
                  y.max = textureMaximum[y.drawableId] ? textureMaximum[y.drawableId].item : 0;
                } else if (y.textureId) {
                  y.max = textureMaximum[y.textureId] ? textureMaximum[y.textureId].texture : 0;
                } else if (y.propsId) {
                  y.max = textureMaximum[y.propsId] ? textureMaximum[y.propsId].item : 0;
                }
              });
            });
          } else {
            item.options.map((x: any, optIndex: number) => {
              if (x.options) {
                x.options.map((y: any, optIndex2: number) => {
                  if (y.event && textureMaximum[y.event]) {
                    y.max = textureMaximum[y.event].item;
                    if (textureMaximum[y.event].disabled) {
                      if (textureMaximum[y.event].disabled[String(optIndex2 + 1)]) {
                        y.disabled = true;
                      }
                    }
                  }
                });
                if (x.event && textureMaximum[x.event]) {
                  x.max = textureMaximum[x.event].item;
                  if (textureMaximum[x.event].disabled) {
                    Array.from({ length: x.max }).map((_, index: number) => {
                      if (textureMaximum[x.event].disabled[String(index + 1)]) {
                        if (!x.options[index + 1]) x.options[index + 1] = { title: x.event + String(index + 1) };
                        x.options[index + 1].disabled = true;
                      }
                    });
                  }
                }
              } else if (x.event && textureMaximum[x.event]) {
                x.max = textureMaximum[x.event].item;
                if (textureMaximum[x.event].disabled && textureMaximum[x.event].disabled[String(optIndex + 1)]) {
                  x.disabled = true;
                }
              }
            });
          }
        });
        setMenu(newMenu);
        break;
      case 'DEFAULT_VALUES':
        const { Clothes, Skin } = event.data.data;
        const newMenu2: any = [...menu];

        newMenu2.map((item: any, index: number) => {
          if (item.name == 'Outfit') {
            item.options.map((x: any, optIndex: number) => {
              x.options.map((y: any, optIndex2: number) => {
                if (y.drawableId && Clothes[y.drawableId]) {
                  y.value = Clothes[y.drawableId].item || 0;
                } else if (y.textureId && Clothes[y.textureId]) {
                  y.value = Clothes[y.textureId].texture || 0;
                }
              });
            });
          } else {
            item.options.map((x: any, optIndex: number) => {
              if (x.options) {
                x.options.map((y: any, optIndex2: number) => {
                  if (y.event && Skin[y.event]) {
                    y.value = Skin[y.event];
                  }
                });
                if (x.event && Skin[x.event]) {
                  x.value = Skin[x.event];
                }
              } else if (x.event && Skin[x.event]) {
                x.value = Skin[x.event];
              }
            });
          }
        });
        setMenu(newMenu2);
        break;
      case 'UPDATE_CLOTH_TEXTURE':
        const newMenu3: any = [...menu];
        const data = event.data.data;
        newMenu3.map((item: any, index: number) => {
          if (item.name == 'Outfit') {
            item.options.map((x: any, optIndex: number) => {
              x.options.map((y: any, optIndex2: number) => {
                if (y.event === data.event) {
                  y.max = data.texture || 0;
                }
              });
            });
          }
        });
        setMenu(newMenu2);
        break;
      case 'OPEN_BARBERSHOP':
        // setBarbershop(event.data.data);
        const BarberCategories = event.data.data || barbershop;
        const newMenu5 = menu
          .filter((item) => item.name && BarberCategories[item.name])
          .map((item) => ({
            ...item,
            options: item.options
              ?.map((opt) => ({
                ...opt,
                options: opt.options?.filter((y) => y.event && BarberCategories[item.name].includes(y.event)),
              }))
              .filter((opt) => (opt.event && BarberCategories[item.name].includes(opt.event)) || (opt.options && opt.options.length > 0)),
          }))
          .filter((item) => item.options && item.options.length > 0);
        setMenu(newMenu5);
        break;
      case 'UPDATE_GENDER':
        setGenderSelected(event.data.data);
        break;
    }
  };

  const keydown = (event: any) => {
    switch (event.code) {
      case 'KeyD':
        fetchNui('CharacterCreator::RotatePed', { mode: 'right' });
        break;
      case 'KeyA':
        fetchNui('CharacterCreator::RotatePed', { mode: 'left' });
        break;
      case 'Escape':
        fetchNui('Barbershop::Close');
        break;
    }
  };

  useEffect(() => {
    window.addEventListener('message', messageHandler);
    window.addEventListener('keydown', keydown);
    return () => {
      window.removeEventListener('message', messageHandler);
      window.removeEventListener('keydown', keydown);
    };
  }, [messageHandler]);

  const subMenuVariables = async (index: number, optIndex: number, value: any) => {
    const newMenu: any = [...menu];

    newMenu[activeMenu].options[index].options[optIndex].value = value;

    if (newMenu[activeMenu].options !== undefined) {
      await fetchNui(
        'CharacterCreator::MenuVariables',
        {
          menu: newMenu[activeMenu].options[index],
          optIndex,
        },
        {},
      );
    }

    setMenu(newMenu);
  };

  const menuVariables = async (index: number, value: any) => {
    const newMenu: any = [...menu];

    newMenu[activeMenu].options[index].value = value;

    if (newMenu[activeMenu].options !== undefined) {
      await fetchNui('CharacterCreator::MenuVariables', {
        menu: newMenu[activeMenu].options[index],
      });
    }

    setMenu(newMenu);
  };

  const menuErrorVariables = async (index: number, value: any, type: string, index2?: number) => {
    const newMenu: any = [...menu];

    if (type === 'name') newMenu[activeMenu].options[index].error = value;

    if (type === 'old') newMenu[activeMenu].options[index].options[index2 as number].error = value;

    setMenu(newMenu);
  };

  const loadImage = async (imageNumber: number, optIndex: number) => {
    newParentImage[optIndex] = `nui://will_creator_v2/web-side/dist/parents/parent_${Number(imageNumber)}.png`;

    setParentImage(newParentImage);
  };

  const loadDefaultImage = async () => {
    setParentImage([`nui://will_creator_v2/web-side/dist/parents/parent_0.png`, `nui://will_creator_v2/web-side/dist/parents/parent_21.png`]);
  };

  useEffect(() => {
    loadDefaultImage();
  }, []);

  const handleValidateInputs = async () => {
    const usernameMin = 7;
    const usernameMax = 25;
    const usernameRegex = new RegExp(/\b([A-Z-][-,a-z. ']+[ ]*)+/);

    menuErrorVariables(0, '', 'name', 0);
    menuErrorVariables(0, '', 'old', 0);

    // Name
    if (menu![0].options![0].value!.toString().length < usernameMin)
      return menuErrorVariables(0, 'O nome deve conter pelo menos 7 dgitos', 'name', 0);
    if (menu![0].options![0].value!.toString().length > usernameMax)
      return menuErrorVariables(0, 'O nome deve conter no mximo 25 dgitos', 'name', 0);

    if (menu![0].options![0].value!.toString().split(' ').length < 2 || menu![0].options![0].value!.toString().split(' ').length > 3)
      return menuErrorVariables(0, 'O nome deve conter 2 ou 3 nomes', 'name', 0);

    for (let i = 0; i < menu![0].options![0].value!.toString().split(' ').length; i++) {
      if (menu![0].options![0].value!.toString().split(' ')[i].toString().length < 3)
        return menuErrorVariables(0, 'Cada nome deve conter pelo menos 3 dgitos', 'name', 0);

      const firstLetterUppercase = await usernameRegex.test(menu![0].options![0].value!.toString().split(' ')[i].toString());
      if (!firstLetterUppercase) return menuErrorVariables(0, 'Cada nome deve comear com uma letra maiscula', 'name', 0);
    }

    const usernameError = await usernameRegex.test(menu[0].options && (menu[0].options[0].value as any));
    if (!usernameError) return menuErrorVariables(0, 'Nome invlido', 'name', 0);

    // Age
    if ((menu![0].options![0].options![0].value as number) < 18) return menuErrorVariables(0, 'A idade deve ser maior que 18', 'old', 0);
    if ((menu![0].options![0].options![0].value as number) > 98) return menuErrorVariables(0, 'A idade deve ser menor que 98', 'old', 0);
  };

  const handleSaveCharacter = async () => {
    await fetchNui(`CharacterCreator::SaveCharacter`);
  };

  return (
    <Container bg={RodaImage}>
      <SideContainerLeft>
        <WheelMenuComponent
          menuActive={menu[activeMenu]}
          menu={menu}
          menuWrapperRef={menuWrapper}
          containerRef={containerRef}
          menuWrapper={menuWrapper}
          setActiveMenu={setActiveMenu}
          setMenu={setMenu}
        />
      </SideContainerLeft>
      <CenterContainer ref={containerRef}></CenterContainer>
      <SideContainerRight>
        <PageHeader>
          <HeaderText>
            CRIAO DE
            <HeaderTextBold>
              <br></br>PERSONAGEM
            </HeaderTextBold>
          </HeaderText>
          <ServerLogo />
        </PageHeader>
        <ContentContainer>
          {menu[activeMenu].options?.map((item, index) => (
            <>
              <MenuTitle>
                <Strip />
                <Text600>{item.title}</Text600>
                <Strip />
              </MenuTitle>
              <MenuOptionsContainer>
                {item.type == 'input' && (
                  <InputComponent
                    functionOnChange={(e: string) => menuVariables(index, e)}
                    item={item}
                    menuActive={menu[activeMenu]}
                    shouldValidate={true}
                    functionToValidate={handleValidateInputs}
                  />
                )}

                {item.options?.map((option, optIndex) => (
                  <>
                    {option.type == 'input_number' && (
                      <InputSmallComponent
                        functionOnChange={(e: string) => subMenuVariables(index, optIndex, e)}
                        option={option}
                        menuActive={menu[activeMenu]}
                        shouldValidate={true}
                        functionToValidate={handleValidateInputs}
                      />
                    )}
                  </>
                ))}

                {/* Name and Age errors */}
                {activeMenu === 0 && menu[0] && menu[activeMenu].options![index]?.error && (
                  <label style={{ position: 'absolute', fontSize: '0.6rem', bottom: '-4px' }}>
                    <ErrorIcon className="fa fa-warning"></ErrorIcon> {menu[activeMenu].options![index].error}
                  </label>
                )}
                {activeMenu === 0 && menu[activeMenu].options![index].options![0]?.error && (
                  <label style={{ position: 'absolute', fontSize: '0.6rem', bottom: '-4px' }}>
                    <ErrorIcon className="fa fa-warning"></ErrorIcon> {menu[activeMenu].options![index].options![0].error}
                  </label>
                )}

                {item.type == 'select' &&
                  item.options?.map((option, optIndex) => (
                    <InputRadioComponent
                      functionOnClick={() => {
                        menuVariables(index, option.value);
                        setGenderSelected(option.value as string);
                      }}
                      item={item}
                      option={option}
                    />
                  ))}

                {item.type == 'box' &&
                  item.options?.map((option, optIndex) => (
                    <ParentContainer>
                      <ParentImageContainer>
                        <img src={parentImage[optIndex]} alt="" />
                      </ParentImageContainer>
                      <ParentNameContainer>
                        <i
                          className="fa fa-arrow-left"
                          onClick={() => {
                            Number(option.value) > 0 && subMenuVariables(index, optIndex, Number(option.value) - 1);
                            Number(option.value) >= 0 && loadImage(Number(option.value), optIndex);
                          }}
                        ></i>
                        <NormalText>{faceNames[Number(option.value)]}</NormalText>
                        <i
                          className="fa fa-arrow-right"
                          onClick={() => {
                            Number(option.value) < faceNames.length - 1 && subMenuVariables(index, optIndex, Number(option.value) + 1);
                            Number(option.value) < faceNames.length - 1 && loadImage(Number(option.value), optIndex);
                          }}
                        ></i>
                      </ParentNameContainer>
                    </ParentContainer>
                  ))}

                {item.type == 'range' && (
                  <InputContainerBox>
                    {item.options?.map((option, optIndex) => (
                      <InputRangeComponent option={option} functionOnChange={(e) => subMenuVariables(index, optIndex, e)} />
                    ))}
                  </InputContainerBox>
                )}

                {item.type === 'imagepicker' && (
                  <ImagePickerComponent
                    gender={genderSelected}
                    imageFolder={item.imageFolder}
                    options={item}
                    functionOnClick={(optIndex) => menuVariables(index, optIndex)}
                  />
                )}

                {item.type === 'colorpicker' && (
                  <ColorPickerComponent colors={item.colors} functionOnClick={(colorIndex) => menuVariables(index, colorIndex)} />
                )}

                {item.type == 'rangeWithInput' && (
                  <InputContainerBox>
                    {item.options?.map((option, optIndex) => (
                      <>
                        <InputContainerTitle>{option.title}</InputContainerTitle>
                        <InputTypeRangeAndTexts>
                          <Row>
                            <p>{option.min}</p>
                            <InputSmallComponent
                              max={option.max}
                              option={option}
                              functionOnChange={(e: string) => subMenuVariables(index, optIndex, e)}
                              shouldValidate={false}
                              value={option.value}
                            />
                            <p>{option.max}</p>
                          </Row>
                          <InputRangeComponent
                            justInput={true}
                            option={option}
                            max={option.max}
                            functionOnChange={(e) => subMenuVariables(index, optIndex, e)}
                          />
                        </InputTypeRangeAndTexts>
                      </>
                    ))}
                  </InputContainerBox>
                )}
              </MenuOptionsContainer>
            </>
          ))}
        </ContentContainer>

        <ButtonsContainer>
          <Button onClick={handleSaveCharacter}>SALVAR PERSONAGEM</Button>
        </ButtonsContainer>
      </SideContainerRight>
    </Container>
  );
}
