import { useState } from 'react';
import { CSSTransition } from 'react-transition-group';
import { ICharacter } from '../AdmChars';
import { fetchNui } from '../../../helpers/fetchNui';
import { Button, DeleteCharacterContainer } from '../AdminPainel.Style';

interface ICharacterProps {
  character: ICharacter;
}

export function DelChar({ character }: ICharacterProps) {
  const [show, setShow] = useState(false);
  return (
    <>
      <CSSTransition in={show} timeout={800} classNames="fade" unmountOnExit>
        <DeleteCharacterContainer>
          <p>
            Tem certeza que deseja o personagem{' '}
            <b>
              {character.name} ({character.id})?
            </b>
          </p>
          <div>
            <Button onClick={() => setShow(false)}>Cancelar</Button>
            <Button
              onClick={async () => {
                const result = await fetchNui('deleteCharacter', { id: character.id });
                if (result) {
                  setShow(false);
                }
              }}
            >
              Deletar
            </Button>
          </div>
        </DeleteCharacterContainer>
      </CSSTransition>
      <Button
        onClick={() => {
          setShow(!show);
        }}
      >
        Deletar personagem
      </Button>
    </>
  );
}
