import { OptionContainer } from '../../../pages/Character/Creator.Style';
import { InputTypeRadio, LabelFor } from './InputRadio.Style';

interface IInputRadio {
  option: any;
  item: any;
  functionOnClick: () => void;
}

export function InputRadioComponent({ option, functionOnClick, item }: IInputRadio) {
  return (
    <OptionContainer active={option.value == item.value} onClick={functionOnClick}>
      <InputTypeRadio name="genero" type="radio" id={option.value} value={option.value} checked={option.value == item.value}></InputTypeRadio>
      <LabelFor for={option.value}>{option.label}</LabelFor>
    </OptionContainer>
  );
}
