import { useEffect, useState } from 'react';
import { Strip } from '../../components/Strip/Strip';
import {
  Button,
  InfoContainer,
  CharacterContainer,
  CharacterInfo,
  CharactersContainer,
  Container,
  ContentContainer,
  HeaderContainer,
  CharacterAge,
  InfosContainer,
  AdminGoBack,
} from './AdminPainel.Style';
import { EditChar } from './components/EditChar';
import { fetchNui } from '../../helpers/fetchNui';
import { useNavigate } from 'react-router-dom';

export interface ICharacter {
  name: string;
  age: number;
  id: number;
  online: boolean;
  image?: string;
}

export function AdmChars() {
  const navigate = useNavigate();

  const [showPainel, setShowPainel] = useState(true);
  const [characters, setCharacters] = useState<ICharacter[]>([
    {
      name: 'Will Reborn',
      age: 25,
      online: true,
      id: 1,
    },
  ]);

  const keydown = (event: any) => {
    switch (event.code) {
      case 'Escape':
        fetchNui('AdminPainel::Close');
        break;
    }
  };

  useEffect(() => {
    fetchNui('AdminPainel::GetCharacters').then((data) => {
      setCharacters(data);
    });
  }, []);

  useEffect(() => {
    window.addEventListener('keydown', keydown);
    return () => {
      window.removeEventListener('keydown', keydown);
    };
  }, [keydown]);

  return (
    <Container
      style={{
        justifyContent: showPainel ? 'center' : 'flex-start',
        height: '100vh',
        width: showPainel ? '100vw' : '20vw',
      }}
    >
      {showPainel && (
        <ContentContainer
          style={{
            position: 'relative',
          }}
        >
          <AdminGoBack
            onClick={() => {
              navigate('/Admin', { replace: true });
            }}
          >
            Voltar
          </AdminGoBack>
          <HeaderContainer>
            <Strip />
            <h2>
              <b>Personagens</b>
            </h2>
            <Strip />
          </HeaderContainer>
          <CharactersContainer>
            {characters.map((character) => (
              <CharacterContainer>
                <CharacterInfo>
                  <img
                    src={character.image || ''}
                    alt=""
                    onError={(e: any) => {
                      e.target.src = 'https://uxwing.com/wp-content/themes/uxwing/download/peoples-avatars/no-profile-picture-icon.png';
                      e.onerror = null;
                    }}
                  />
                  <InfosContainer>
                    <InfoContainer>
                      <span>{character.id}</span>
                      <Strip />
                      <span>{character.name}</span>
                    </InfoContainer>
                    <CharacterAge>{character.age} anos</CharacterAge>
                  </InfosContainer>
                </CharacterInfo>
                <InfoContainer>
                  <Button
                    onClick={() => {
                      setShowPainel(false);
                      fetchNui('AdminPainel::ViewCharacter', { id: character.id });
                    }}
                  >
                    Visualizar
                  </Button>
                  <EditChar character={character as ICharacter} />
                </InfoContainer>
              </CharacterContainer>
            ))}
          </CharactersContainer>
          <p>Total: {characters.length}</p>
        </ContentContainer>
      )}
      {!showPainel && (
        <ContentContainer
          style={{
            height: !showPainel && '20%',
          }}
        >
          <HeaderContainer>
            <Strip />
            <h2>
              <b>Admin Painel</b>
            </h2>
            <Strip />
          </HeaderContainer>
          <Button
            style={{ marginTop: '2vh' }}
            onClick={() => {
              setShowPainel(true);
              fetchNui('AdminPainel::DeleteChar');
            }}
          >
            Voltar
          </Button>
        </ContentContainer>
      )}
    </Container>
  );
}
