import { useState } from 'react';
import { BlockedWarning, BlockedWarningButton, BlockedWarningButtonsContainer, ImageButton, ImagesContainer } from './ImagePicker.Style';
import { CSSTransition } from 'react-transition-group';

interface IImagePickerComponent {
  gender: string;
  options: any;
  imageFolder?: string;
  functionOnClick: (optIndex: number) => void;
}

export function ImagePickerComponent({ gender, options, imageFolder, functionOnClick }: IImagePickerComponent) {
  const Gender = gender === 'mp_m_freemode_01' ? 'male' : 'female';
  const [showWarning, setShowWarning] = useState(false);
  return (
    <ImagesContainer>
      <CSSTransition in={showWarning} timeout={800} classNames="fade" unmountOnExit>
        <BlockedWarning>
          <h3>Customizao exclusiva para VIP</h3>
          <p>
            <b>Negado!</b> Essa customizao  exclusiva para VIPs, adquira em nossa loja para liberar o slot
          </p>
          <BlockedWarningButtonsContainer>
            <BlockedWarningButton onClick={() => setShowWarning(!showWarning)}>Entendido</BlockedWarningButton>
          </BlockedWarningButtonsContainer>
        </BlockedWarning>
      </CSSTransition>
      {options.min === -1 && (
        <ImageButton
          onClick={(e: any) => {
            functionOnClick(-1);
          }}
        >
          <img
            src={`nui://will_creator_v2/web-side/dist/${Gender}/${imageFolder}/-1.png`}
            alt={'Image'}
            style={{
              opacity: 1.0,
              cursor: 'pointer',
            }}
          />
        </ImageButton>
      )}
      {Array.from({ length: options.max }, (_, i) => {
        const option = options.options[i];
        return (
          <ImageButton
            onClick={(e: any) => {
              if (option?.disabled) {
                setShowWarning(true);
                return;
              }
              functionOnClick(i);
            }}
          >
            <img
              src={`nui://will_creator_v2/web-side/dist/${Gender}/${imageFolder}/${i}.png`}
              alt={option?.title || 'Image'}
              style={{
                opacity: option?.disabled ? 0.2 : 1.0,
                cursor: option?.disabled ? 'default' : 'pointer',
              }}
              onError={(e: any) => {
                e.target.src = 'https://uxwing.com/wp-content/themes/uxwing/download/peoples-avatars/no-profile-picture-icon.png';
                e.onerror = null;
              }}
            />
          </ImageButton>
        );
      })}
    </ImagesContainer>
  );
}
