import { useState } from 'react';
import { CSSTransition } from 'react-transition-group';
import { Button, EditCharacterContainer, InfoContainer, SliderToggleStyle } from '../AdminPainel.Style';
import { Strip } from '../../../components/Strip/Strip';
import { fetchNui } from '../../../helpers/fetchNui';
import PlusIcon from '../icons/PlusIcon';
import { IBarbershop } from '../AdmBarber';

const SliderToggle = ({ checked, setChecked }: { checked: boolean; setChecked: (checked: boolean) => void }) => {
  return (
    <div
      style={{
        position: 'relative',
        width: '3vw',
        height: '30px',
        margin: '0.7vw',
      }}
    >
      <input
        type="checkbox"
        checked={checked}
        onChange={(e) => setChecked((e.target as HTMLInputElement).checked)}
        style={{ display: 'none' }}
        id="slider-toggle"
      />
      <SliderToggleStyle htmlFor="slider-toggle" />
    </div>
  );
};

export function CreateBarber() {
  const [barbershop, setBarbershop] = useState<IBarbershop>({
    id: 0,
    name: '',
    blip: false,
    coords: { x: 0, y: 0, z: 0 },
  });
  const [show, setShow] = useState(false);
  return (
    <>
      <CSSTransition in={show} timeout={800} classNames="fade" unmountOnExit>
        <EditCharacterContainer>
          <h2 onClick={() => setShow(false)}>X</h2>
          <InfoContainer style={{ gap: '1vw' }}>
            <Strip />
            <h1>Criao de Barbearia</h1>
            <Strip />
          </InfoContainer>
          <InfoContainer>
            <label
              htmlFor="name"
              style={{
                textAlign: 'center',
                width: '20%',
                fontSize: '1.2vw',
                fontWeight: 'bold',
                color: '#fff',
                textTransform: 'uppercase',
              }}
            >
              Nome
            </label>
            <input
              type="text"
              name="name"
              id="name"
              value={barbershop.name}
              onChange={(e) => {
                setBarbershop({ ...barbershop, name: (e.target as HTMLInputElement).value });
              }}
              style={{
                width: '30%',
                height: '2vh',
                borderRadius: '0.5vh',
                border: 'none',
                outline: 'none',
                padding: '0.5vh',
                fontSize: '1vw',
                fontWeight: 'bold',
                color: '#fff',
                backgroundColor: 'rgba(255, 255, 255, 0.2)',
                margin: '0.4vw',
              }}
            />
            <label
              htmlFor="blip"
              style={{
                textAlign: 'center',
                width: '10%',
                fontSize: '1.2vw',
                fontWeight: 'bold',
                color: '#fff',
                textTransform: 'uppercase',
              }}
            >
              Blip
            </label>
            <SliderToggle checked={barbershop.blip} setChecked={(checked) => setBarbershop({ ...barbershop, blip: checked })} />
          </InfoContainer>
          <Button
            onClick={() => {
              fetchNui('AdminPainel::RegisterBarbershops', barbershop);
            }}
          >
            Registrar barbearia
          </Button>
        </EditCharacterContainer>
      </CSSTransition>
      <Button
        style={{
          display: 'flex',
          position: 'absolute',
          top: '1vh',
          right: '0.5vh',
          cursor: 'pointer',
          fontSize: '1vw',
          fontWeight: 'bold',
          color: '#fff',
          transition: '0.5s',
          alignItems: 'center',
          justifyContent: 'center',
          backgroundColor: 'rgba(255, 255, 255, 0.4)',
          padding: '0.3vw',
          borderRadius: '0.5vh',
          width: 'fit-content',
          height: 'fit-content',
          gap: '0.5vw',
        }}
        onClick={() => {
          setShow(!show);
        }}
      >
        <PlusIcon />
        Criar barbearia
      </Button>
    </>
  );
}
