import { useState, Dispatch } from 'react';
import { CSSTransition } from 'react-transition-group';
import { fetchNui } from '../../../helpers/fetchNui';
import { Button, DeleteCharacterContainer } from '../AdminPainel.Style';
import { IBarbershop } from '../AdmBarber';
import TrashIcon from '../icons/TrashIcon';
import { StateUpdater } from 'preact/hooks';

interface IDelBarber {
  id: number;
  name: string;
  coords: { x: number; y: number; z: number };
  blip: boolean;
  setBarbershops: Dispatch<StateUpdater<IBarbershop[]>>;
}

export function DelBarber(data: IDelBarber) {
  const [show, setShow] = useState(false);
  return (
    <>
      <CSSTransition in={show} timeout={800} classNames="fade" unmountOnExit>
        <DeleteCharacterContainer>
          <p>
            Tem certeza que deseja a barbearia{' '}
            <b>
              {data.name} ({data.id})?
            </b>
          </p>
          <div>
            <Button onClick={() => setShow(false)}>Cancelar</Button>
            <Button
              onClick={async () => {
                const result = await fetchNui('AdminPainel::DeleteBarbershops', { id: data.id });
                if (result) {
                  setShow(false);
                  data.setBarbershops([]);
                }
              }}
            >
              Deletar
            </Button>
          </div>
        </DeleteCharacterContainer>
      </CSSTransition>
      <Button
        style={{
          width: '100%',
          height: '100%',
          borderRadius: 0,
          backgroundColor: 'transparent',
        }}
        onClick={() => {
          setShow(!show);
        }}
      >
        <TrashIcon />
      </Button>
    </>
  );
}
